<template>
    <div class="mine">
        <!-- 头部icon -->
        <header class="clearfix">
            <van-nav-bar title="个人中心" left-arrow :border="false" @click-left="back">/></van-nav-bar>
            <div class="setting"></div>
        </header>

        <!-- 主体内容 -->
        <div class="minebox">

            <div class="nav">
                <div class="top clearfix">
                    <div class="nickname fl">
                        <div class="one-txt">{{nickname}}</div>
                        <div class="phone">
                            <i class="fl"></i>
                            {{phone}}
                        </div>
                    </div>
                    <div class="avatar fr">
                        <img class="autoimg" src="../assets/images/snow.jpg" alt="">
                    </div>
                </div>

                <div class="vip">
                    <div class="privilege clearfix">
                        <span class="fl">会员特权</span>
                        <span class="fr">去开通</span>
                    </div>
                    <div>购物双倍积分，享全场包邮</div>
                </div>

                <div class="order clearfix">
                    <span class="fl">
                        <i class="fl"></i>
                        我的订单
                    </span>
                    <span class="fr">查看全部订单</span>
                </div>

                <div class="list">
                    <ul class="clearfix">
                        <li>
                            <i class="wallet"></i>
                            待付款
                        </li>
                        <li>
                            <i class="daishouhuo"></i>
                            待收货
                        </li>
                        <li>
                            <i class="comment"></i>
                            待评论
                        </li>
                        <li>
                            <i class="shouhou"></i>
                            售后
                        </li>
                    </ul>
                </div>
            </div>

            <div class="minelist">
                <ul>
                    <li class="clearfix">
                        <div class="icon fl">
                            <i class="collect"></i>
                        </div>
                        <div class="fl">
                            <span>我的收藏</span>
                            <i class="fr"></i>
                        </div>
                    </li>
                    <li class="clearfix">
                        <div class="icon fl">
                            <i class="coupon"></i>
                        </div>
                        <div class="fl">
                            <span>我的优惠券</span>
                            <i class="fr"></i>
                        </div>
                    </li>
                    <li class="clearfix">
                        <div class="icon fl">
                            <i class="modify"></i>
                        </div>
                        <div class="fl">
                            <span>个人信息</span>
                            <i class="fr"></i>
                        </div>
                    </li>
                    <li class="clearfix">
                        <div class="icon fl">
                            <i class="about"></i>
                        </div>
                        <div class="fl">
                            <span>关于我们</span>
                            <i class="fr"></i>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    // http://127.0.0.1:8080/#/mine

    export default {
        name:'mine',

        data() {
            return {
                nickname:'',
                phone:''
            }
        },

        created(){

            //进行身份验证
            this.axios({
                method: 'GET',
                url: '/user', //路径设置放在main.js中
                params: {
                    key: 'default'
                }
            }).then(result =>{
                // console.log('user result =>',result);    
                this.nickname = result.data.data[0].nickname;
                this.phone = result.data.data[0].phone;

            }).catch( err=>{
                // console.log('出错');
            })
        },

        methods:{
            // 返回上一步
            back() {
                this.$router.go(-1);
            },
        }
    }
</script>

<style lang="less" scoped>
    .mine{
        header{
            width: 100%;
            // background-image:linear-gradient(#FE9702, #FFA602,#FEDA00,#FEDA00);
            background-color: #f5f5f5;
            position: relative;
            /deep/.van-nav-bar{
              background-color: transparent;
            }
            .van-nav-bar__title{
              color: #000;
              font-size: 16px;
            }
            .van-icon-arrow-left:before{
               color: #888;
               font-size: 22px;
            }

            .setting{
                position: absolute;
                right: 3px;
                top: 4px;
                width: 30px;
                height: 30px;
                background: url("../assets/images/setting.png") no-repeat center center;
                background-size: 30px 30px;
            }
        }

        .minebox{
            position: fixed;
            top: 50px;
            left: 0;
            right: 0;
            bottom: 50px;
            background-color: #f5f5f5;
        }

        .nav{
            padding: 10px;
            background-color: #fff;
            .top{
                padding: 10px 6px;
                .nickname{
                    width: calc(~'100% - 100px');
                    font-size: 18px;
                    .phone{
                        >i{
                            display: block;
                            width: 22px;
                            height: 22px;
                            background: url("../assets/images/phone.png") no-repeat center center;
                            background-size: 22px 22px;
                        }
                        padding: 8px 0;
                        color: #666;
                        font-size: 14px;
                        line-height: 22px;
                    }
                }
                .avatar{
                    width: 68px;
                    height: 68px;
                    background-color: #f5f5f5;
                    border-radius: 50%;
                    >img{
                        border-radius: 50%;
                    }
                }
            }

            .vip{
                background-color: #222834;
                border-radius: 8px;
                >div{
                    padding: 15px 10px;
                    color: #ECCD94;
                    font-size: 16px;
                }
                .privilege{
                    span{
                        padding: 0px 6px;
                        &:first-child{
                            color: #F3DB75;
                            font-size: 18px;
                        }
                        &:last-child{
                            color: #ECCD94;
                            font-size: 14px;
                        }
                   
                    }
                }

            }

            .order{
                padding: 15px 10px;
                border-bottom: 1px solid #ddd;
                span{
                    font-size: 16px;
                    line-height: 26px;
                    &:first-child{
                        >i{
                            display: block;
                            width: 26px;
                            height: 26px;
                            background: url("../assets/images/order.png") no-repeat center center;
                            background-size: 26px 26px;
                            padding: 0 6px;
                        }
                    }
                }
            }

            .list{
                padding-top: 10px;
                ul li{
                    float: left;
                    width: 25%;
                    text-align:center;
                    font-size: 14px;
                    >i{
                        display: block;
                        width: 30px;
                        height: 30px;
                        margin: 0 auto;
                    }
                    .wallet{
                        background: url("../assets/images/wallet.png") no-repeat center center;
                        background-size:30px 30px;
                    }
                    .daishouhuo{
                        background: url("../assets/images/daishouhuo.png") no-repeat center center;
                        background-size:30px 30px;
                    }
                    .comment{
                        background: url("../assets/images/comment.png") no-repeat center center;
                        background-size:30px 30px;
                    }
                    .shouhou{
                        background: url("../assets/images/shouhou.png") no-repeat center center;
                        background-size:28px 28px;
                    }
                }
            }
        }

        .minelist{
            margin:10px 0;
            padding-bottom: 20px;
            background-color: #fff;

            ul li{
                padding: 10px;
                .icon{
                    width: 14px;
                    height: 14px;
                    background-image:linear-gradient(#FEDA00,#FEDA00,#F4BD32,#FFA602);
                    border-radius:6px;
                    position: relative;
                    >i{
                        display: block;
                        width: 20px;
                        height: 20px;
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom:0;
                        margin: auto;
                    }
                }

                .collect{
                    background:url("../assets/images/collect.png") no-repeat center center;
                    background-size: 24px 24px;
                }
                .coupon{
                    background:url("../assets/images/coupon.png") no-repeat center center;
                    background-size: 22px 22px;
                }
                .modify{
                    background:url("../assets/images/modify.png") no-repeat center center;
                    background-size: 24px 24px;
                }
                .about{
                    background:url("../assets/images/about.png") no-repeat center center;
                    background-size: 22px 22px;
                }

                >div{
                    padding:10px;
                    width: calc(~'100% - 80px');
                    border-bottom: 1px solid #ddd;
                    line-height: 20px;
                    font-size: 14px;
                    >i{
                        display: block;
                        width: 20px;
                        height: 20px;
                        background:url("../assets/images/right.png") no-repeat center center;
                        background-size: 20px 20px;
                    }
                }
            }

        }

    }

</style>